<template>
  <v-container fluid>
    <v-row>
      <v-col>
        <v-data-table
          :headers="headers"
          :items="filledOrders"
          :sort-by="['id']"
          :sort-desc="[true, false]"
          :items-per-page="10"
          class="elevation-2"
          :search="search"
        >
        </v-data-table>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import NProgress from 'nprogress'
import { mapState } from 'vuex'
import moment from 'moment'
moment.locale('ru')
export default {
  name: 'OrdersList',
  data() {
    return {
      search: '',
      headers: [
        {
          text: '№',
          align: 'start',
          value: 'id'
        },
        {
          sortable: false,
          text: 'Имя пользователя',
          value: 'user.full_name'
        },
        {
          sortable: false,
          text: 'Название книги',
          value: 'book.title'
        },
        {
          sortable: false,
          text: 'Автор книги',
          value: 'book.author'
        },
        {
          sortable: false,
          text: 'ISBN',
          value: 'book.isbn'
        },
        {
          sortable: false,
          text: 'Сдал',
          value: 'retrieved'
        },
        {
          sortable: false,
          text: 'Время сдачи',
          value: 'time_of_pass'
        }
      ]
    }
  },
  computed: {
    ...mapState(['order']),
    filledOrders() {
      return this.order.orders.map(order => {
        if (order.retrieved === false) {
          order.retrieved = 'Не сдал'
          order.time_of_pass = ''
        }
        if (order.retrieved === true) {
          order.retrieved = 'Сдал'
          order.time_of_pass = moment().format()
        }
        return { ...order }
      })
    }
  },
  created() {
    NProgress.start()
    this.$store.dispatch('order/fetchOrders')
  },
  mounted() {
    NProgress.done()
  }
}
</script>

